@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* border: 1px solid rgb(173, 113, 0); */
    color: white;
}

header{
    display: flex;
    height: 60px;
    width: 100%;
    /* background-color: rgb(0, 0, 0); */
    background-color: transparent;
    align-items: center;
}

header h1{
    margin-left: 1em;
}

header ul{
    display: flex;
    margin-left: 70em;
    margin-top: -3em;
}

header ul Button{
    margin-right: 5em;
    list-style: none;
    font-size: 20px;
    font-weight: bold;
}
header ul button{
    margin-right: 2em;
    list-style: none;
    font-size: 20px;
    font-weight: bold;
}

header ul li a{
    text-decoration: none;
}

.landing-page{
    /* height: calc(100vh - 60px); */
    height: 100vh;
    width: 100%;
    /* background-color: brown; */
    background-size: cover;
    /* overflow: hidden; */
    position: relative;
    display: flex;
    align-items: center;
    /* justify-content: center; */
}

.landing-page #video{
    position: absolute;
    /* height: calc(100vh - 60px); */
    height: 120vh;
    width: 100%;
    background-size: cover;
    margin-top: -10vh;
    z-index: -10;
    /* background-color: red; */
}

.heading{
    margin-top: -13em;
    margin-left: 4em;
    position: absolute;
    z-index: 99;
    color: black;
    /* mix-blend-mode: exclusion; */
}

.heading h1{
    mix-blend-mode: exclusion;
    font-size: 100px;
    /* outline: red; */
    /* color: transparent; */
    /* text-shadow: 0 0 2px red; */
    /* mix-blend-mode: multiply; */
}

.heading h2{
    mix-blend-mode: exclusion;
    font-size: 40px;
}

.heading h5 a{
    color: rgb(255, 0, 0);
}
#About{
    height: 60vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
}
#About .heading h1{
    color: red;
    font-size: x-large;
    font-weight: bold;
    border-bottom: 3px solid red;
    margin-bottom: 2em;
    margin-right: 3em;
    
}

.about-box{
    margin-top: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
}

#About .box{
    height: 200px;
    width: 200px;
    background-color: white;
    margin: 4em;
    border-radius: 20px;
    box-shadow: 20px 20px 60px #d9d9d9, -20px -20px 60px #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
    color: lightseagreen;
    flex-direction: column;
    font-size: 20px;
}

#About .box:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    box-shadow: none;
}

#color{
    fill: red;
    font-size: 80px;
    padding: 7px;
}
#Skills {
    margin-top: 2em;
    height: 80vh;
    width: 100%;
    /* background-color: red; */
    /* margin-top: 10em; */
}

#Skills .heading h1 {
    color: red;
    font-size: 30px;
    border-bottom: 3px solid red;
    margin: 11rem 0 0 39rem;
}

.skills-box {
    /* margin-top: 4em; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.skills-box {
    display: flex;
    display: grid;
    align-items: center;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.skills-box .box {
    height: 180px;
    width: 180px;
    background-color: rgb(255, 255, 255);
    background-color: transparent;
    /* border: 2px solid white; */
    margin: 2em;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
    /* color: rgb(255, 255, 255); */
    flex-direction: column;
    font-size: 30px;
    /* animation: move 2s linear infinite; */
    font-family: 'Righteous', cursive;
    box-shadow: 20px 20px 60px #d9d9d9, -20px -20px 60px #ffffff;
    padding: 5px;
}

.skills-box .box span{
    margin-top: 8em;
    color: lightseagreen;
}

.skills-box .box:hover{
    margin-top: -1em;
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    color: lightseagreen;
}

.skills-box .left{
    -webkit-animation: moveleft 2s linear infinite;
            animation: moveleft 2s linear infinite;
}
.skills-box .right{
    -webkit-animation: moveleft 2s linear infinite;
            animation: moveleft 2s linear infinite;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@-webkit-keyframes moveleft{
    to{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    25%{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    } 
    50%{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    } 
    75%{
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
    } 
    100%{ 
        -webkit-transform: translateX(0%); 
                transform: translateX(0%);
    } 
}

@keyframes moveleft{
    to{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    25%{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    } 
    50%{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    } 
    75%{
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
    } 
    100%{ 
        -webkit-transform: translateX(0%); 
                transform: translateX(0%);
    } 
}

@-webkit-keyframes moveright{
    to{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    25%{
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
    } 
    50%{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    } 
    75%{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    } 
    100%{ 
        -webkit-transform: translateX(0%); 
                transform: translateX(0%);
    } 
}

@keyframes moveright{
    to{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    25%{
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
    } 
    50%{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    } 
    75%{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    } 
    100%{ 
        -webkit-transform: translateX(0%); 
                transform: translateX(0%);
    } 
}

@-webkit-keyframes moveup{
    0%{
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50%{
        -webkit-transform: translateY(-8%);
                transform: translateY(-8%);
    }
    100%{
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes moveup{
    0%{
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
